<template>
  <div class="ssq_card">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="float:left;color:#409EFF">双色球</span>
        <br>
        <br>
        <span style="float:right;color:#409EFF">{{"第"+last_info.expect+"期"}}</span>
        <br>
        <br>
        <el-button v-for="item in last_info.red" :key="item" type="danger" circle>{{item+' '}}</el-button>
        <el-button type="primary" circle>{{last_info.blue}}</el-button>
        <!-- <div class="text item" style="float:left"> {{last_info}}</div>
        <div class="text item" style="float:left"> {{last_info}}</div> -->
      </div>
      <span style="float:left;color:#409EFF">今日推荐</span>
      <br>
      <br>
      <div v-for="item in lucky_info" :key="item" class="text item">
        <el-button v-for="red_item in item.red" :key="red_item" type="danger" circle>{{red_item+' '}}</el-button>
        <el-button type="primary" circle>{{item.blue}}</el-button>
        <br>
        <br>
      </div>
    </el-card>
    <br>
    <br>
    <el-button type="primary" style="float:left" @click="update">Update</el-button>
  </div>
</template>
<style>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 5px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }
</style>

<script>
import axios from 'axios';
export default {
  name: 'HelloWorld',
  data() {
    return {
      last_info: '',
      lucky_info: ''
    };
  },
  methods: {
    prefixInteger(num, n) {
      return (Array(n).join(0) + num).slice(-n);
    },

    getList() {
      axios.get('http://192.168.1.12:8888/lucky').then(response => {
        console.log(response.data);
        var data = response.data;
        this.last_info = data.Last;
        this.lucky_info = data.Lucky;
        for (let i = 0; i < this.last_info.red.length; i++) {
          this.last_info.red[i] = this.prefixInteger(this.last_info.red[i], 2);
        }
        this.last_info.blue = this.prefixInteger(this.last_info.blue, 2);
        for (let j = 0; j < this.lucky_info.length; j++) {
          this.lucky_info[j].blue = this.prefixInteger(this.lucky_info[j].blue, 2);
          for (let k = 0; k < this.lucky_info[j].red.length; k++) {
            this.lucky_info[j].red[k] = this.prefixInteger(this.lucky_info[j].red[k], 2);
          }
        }
      });
    },
    update() {
      axios.get('http://192.168.1.12:8888/update').then(response => {
        if (response.Status === 200) {
          this.$notify({
            title: '更新成功',
            message: '彩票信息更新成功',
            type: 'success'
          });
          location.reload();
        } else {
          console.log(response);
        }
      });
    }
  },
  created() {
    this.getList();
  }
};
</script>



